import React, {Component} from 'react';
import {Tab, Tabs, Grid, Cell, Card, CardTitle, CardActions, Button, CardMenu, IconButton, CardText} from "react-mdl";


class Projects extends Component {
    constructor(props) {
        super(props);
        this.state = {activeTab: 0};
    }


    toogleCategories() {
        if (this.state.activeTab === 0) {
            return (
                <div className="projects-grid">
                    {/*Project 1*/}
                    <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                        <CardTitle style={{
                            color: '#000', height: '176px', background:
                                'url(https://www.segodnya.ua/img/forall/users/3217/321794/new/__44.jpg) center / contain no-repeat'
                        }}>Holdgroup 24</CardTitle>
                        <CardText>
                            Ukrainian holding company
                        </CardText>
                        <CardActions border style={{textAlign: 'center'}}>
                            <Button><a href='https://holdgroup24.space' rel="noopener noreferrer" target="_blank">Live
                                Demo</a></Button>
                        </CardActions>
                    </Card>

                    {/*Project 2*/}
                    <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                        <CardTitle style={{
                            color: '#111', height: '176px', background:
                                'url(data:image/png;base64,) center / contain no-repeat'
                        }}>Olekcher24</CardTitle>
                        <CardText>
                            Personal web-site
                        </CardText>
                        <CardActions border style={{textAlign: 'center'}}>
                            <Button><a href='https://olekcher24.space' rel="noopener noreferrer" target="_blank">Live
                                Demo</a></Button>
                        </CardActions>
                    </Card>

                </div>

            )
        } else if (this.state.activeTab === 1) {
            return (
                <div className="projects-grid">
                    {/*Project 1*/}
                    <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                        <CardTitle style={{
                            color: '#111', height: '176px', background:
                                'url(data:image/jpeg;base64,) center / contain no-repeat'
                        }}>Pinki-Bow</CardTitle>
                        <CardText>
                            Online store of gifts, sweets and teddy bears
                        </CardText>
                        <CardActions border style={{textAlign: 'center'}}>
                            <Button><a href='https://pinkibow.space' rel="noopener noreferrer"
                                       target="_blank">Live Demo</a></Button>
                        </CardActions>
                    </Card>

                    {/*Project 2*/}
                    <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                        <CardTitle style={{
                            color: '#fff', height: '176px', background:
                                'url(https://i1.sndcdn.com/avatars-000565888422-9ndbf3-t500x500.jpg) center / cover'
                        }}>Rabinovich TV</CardTitle>
                        <CardText>
                            News portal
                        </CardText>
                        <CardActions border style={{textAlign: 'center'}}>
                            <Button><a href='https://rabinovichtv.com' rel="noopener noreferrer"
                                       target="_blank">Live Demo</a></Button>
                        </CardActions>
                    </Card>
                </div>
            )
        } else if (this.state.activeTab === 2) {
            return (
                <div className="projects-grid">
                    {/*Project 1*/}
                    <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                        <CardTitle style={{
                            color: '#fff', height: '176px', background:
                                'url(https://bitemycoin.com/wp-content/uploads/2017/09/Top-10-Cryptocurrency-Exchanges-for-Bitcoin.png) center / cover'
                        }}>Cryptocurrency converter</CardTitle>
                        <CardText>
                            Cryptocurrency conversion application
                        </CardText>
                        <CardActions border style={{textAlign: 'center'}}>
                            <Button><a href='https://github.com/vspv/converter' rel="noopener noreferrer"
                                       target="_blank">GitHub</a></Button>
                            <Button><a href='https://vspv.github.io/crypto-converter' rel="noopener noreferrer"
                                       target="_blank">GitHub Pages</a></Button>
                        </CardActions>
                        <CardMenu style={{color: '#fff'}}>
                            <IconButton href='https://github.com/vspv/converter' name="share"/>
                        </CardMenu>
                    </Card>

                    {/*Project 2*/}
                    <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                        <CardTitle style={{
                            color: '#111', height: '176px', background:
                                'url(https://cdn2.iconfinder.com/data/icons/weather-flat-14/64/weather02-512.png) center / cover'
                        }}>Weathereum</CardTitle>
                        <CardText>
                            Weather forecast app
                        </CardText>
                        <CardActions border style={{textAlign: 'center'}}>
                            <Button><a href='https://github.com/vspv/weather_app' rel="noopener noreferrer"
                                       target="_blank">GitHub</a></Button>
                            <Button><a href='https://vspv.github.io/weather_app/' rel="noopener noreferrer"
                                       target="_blank">GitHub Pages</a></Button>
                        </CardActions>
                        <CardMenu style={{color: '#fff'}}>
                            <IconButton href='https://github.com/vspv/weather_app' name="share"/>
                        </CardMenu>
                    </Card>

                    {/*Project 3*/}
                    <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                        <CardTitle style={{
                            color: '#fff', height: '176px', background:
                                'url(https://m.economictimes.com/thumb/msid-74691219,width-1200,height-900,resizemode-4,imgsize-1627763/istock-506551290.jpg) center / cover'
                        }}>Coronavirus statistics app</CardTitle>
                        <CardText>
                            Application for viewing infected, recovered and ded from coronavirus
                        </CardText>
                        <CardActions border style={{textAlign: 'center'}}>
                            <Button><a href='https://github.com/vspv/corona_statistics' rel="noopener noreferrer"
                                       target="_blank">GitHub</a></Button>
                            <Button><a href='https://vspv.github.io/corona_statistics/' rel="noopener noreferrer"
                                       target="_blank">GitHub Pages</a></Button>
                        </CardActions>
                        <CardMenu style={{color: '#fff'}}>
                            <IconButton href='https://github.com/vspv/corona_statistics' name="share"/>
                        </CardMenu>
                    </Card>
                </div>
            )
        }
    }


    render() {
        return (
            <div className="category-tabs">
                <Tabs activeTab={this.state.activeTab} onChange={(tabId) => this.setState({activeTab: tabId})} ripple>
                    <Tab>HTML/CSS</Tab>
                    <Tab>PHP</Tab>
                    <Tab>React</Tab>
                </Tabs>

                <Grid>
                    <Cell col={12}>
                        <div className="content">{this.toogleCategories()}</div>
                    </Cell>
                </Grid>
            </div>
        )
    }
}

export default Projects;